.pretitle {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: 3px;
  color: #c0cdd7;
  text-transform: uppercase;

  &__font {
    color: $white-basic;

    &--red {
      color: $red;
    }
  }

  &__changer-themes {
    font-size: 11px;
  }

  
}
